<template>
  <v-card :key="componentkey">
    <v-data-table
      :headers="headers"
      :items="usreList"
      item-key="full_name"
      class="table-rounded"
      :search="search"
      enable-sort
    >
    <template v-slot:top>
        <v-text-field
          v-model="search"
          outlined
          dense
       
          style="position: fixed;top: 45px;left:30%;width:400px;z-index:1;"
          label="Search"
          class="mx-4"
        >
        
        </v-text-field>
      </template>

      <template v-slot:header.counter >
              <v-select
                :items="items"
                v-model="select"
                label="Select"
                single-line
                item-text="report"
                item-value="src"
                return-object
                persistent-hint
                v-on:input="changeRoute(`${select.src}`)"
              ></v-select>
      </template>
      <template #[`item.firstname`]="{item}">
        {{item.firstname}} &nbsp;&nbsp;&nbsp;{{item.lastname}}
      </template>
      <template #[`item.link`]="{item}">
        <v-btn icon :href="'https://apply.insure/f/'+item.link" target="_blank" color='primary'>
           /{{item.link}}
        </v-btn>
        
      </template>   
     
       <template #[`item.actions`]="{item}">
            
            <v-icon size="20" @click="DelConfirm(item._id)">
                {{mdiDeleteOutline}}
            </v-icon>            
      </template>
    </v-data-table>
    
    <v-dialog
      v-model="dialog"
      max-width="290"
    >
      <v-card>
        <v-card-title class="text-h5">
          Are you remove?
        </v-card-title>
        <v-card-actions>
          <v-spacer></v-spacer>

          <v-btn
            color="green darken-1"
            text
            @click="dialog = false"
          >
            Disagree
          </v-btn>

          <v-btn
            color="green darken-1"
            text
            @click="Delete(),dialog = false"
          >
            Agree
          </v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
  </v-card>  
</template>

<script>
import { mdiSquareEditOutline, mdiDotsVertical } from '@mdi/js'
import data from './datatable-data'
import axios from 'axios'
const BaseUrl = 'https://apply.insure/'
import {
  mdiMagnify,
  mdiAbTesting,
  mdiAbacus,
  mdiAbjadArabic,
  mdiAbjadHebrew,
  mdiAbugidaDevanagari,
  mdiAbugidaThai,
  mdiAccessPoint,
  mdiAccessPointCheck,
  mdiAccessPointMinus,
  mdiAccessPointNetwork,
  mdiAccessPointNetworkOff,
  mdiAccessPointOff,
  mdiAccessPointPlus,
  mdiAccessPointRemove,
  mdiAccountAlertOutline,
  mdiAccountArrowLeftOutline,
  mdiAccountArrowRightOutline,
  mdiAccountBoxMultipleOutline,
  mdiAccountBoxOutline,
  mdiAccountCancelOutline,
  mdiAccountCashOutline,
  mdiAccountCheckOutline,
  mdiAccountChildOutline,
  mdiAccountCircleOutline,
  mdiAccountClockOutline,
  mdiAccountCogOutline,
  mdiAccountDetailsOutline,
  mdiAlarmLightOutline,
  mdiAlertBoxOutline,
  mdiAlertCircleCheckOutline,
  mdiAlertDecagramOutline,
  mdiAlertMinusOutline,
  mdiAlertOutline,
  mdiAlertPlusOutline,
  mdiCheckOutline,
  mdiClipboardOutline,
  mdiClipboardPlayOutline,
  mdiCloseOutline,
  mdiCloudCheckOutline,
  mdiCloudDownloadOutline,
  mdiCogOutline,
  mdiCompassOffOutline,
  mdiContactlessPaymentCircleOutline,
  mdiCrownOutline,
  mdiDeleteOutline,
  mdiDiamondOutline,
  mdiEmailOpenOutline,
  mdiEmoticonHappyOutline,
  mdiFileMultipleOutline,
  mdiFlaskEmptyOutline,
   mdiPencilOutline,
} from '@mdi/js'

export default {
  setup() {
    const statusColor = {
      /* eslint-disable key-spacing */
      Current: 'primary',
      Professional: 'success',
      Rejected: 'error',
      Resigned: 'warning',
      Applied: 'info',
      /* eslint-enable key-spacing */
    }

    return {
      select: { report: '30 Day Total ', src: '30' },
        items: [
          { report: '30 Day Total', src: '30' },
          { report: '15 Day Total', src: '15' },
          { report: '7 Day Total', src: '7' },
          { report: '1 Day Total', src: '' }
        ],
      imageDataAgent: require('@/assets/images/avatars/1.png'),
      imageDataAgency: require('@/assets/images/avatars/default.png'),
      agencyimagename:'',
      agentimagename:'',
      firstName:'',
      link:'',
      lastName:'',
      agencyname:"",
      email:'',
      delId:"",
      createuser:false,
      componentkey:0,
      search: '',
      dialog:false,
      statusitem:'',
      mdiDeleteOutline:mdiDeleteOutline,
      mdiPencilOutline:mdiPencilOutline,
      headers: [
        { text: 'NAME', value: 'firstname' ,width:"200px",},
         { text: 'LINK', value: 'link' ,width:"100px",}, 
          { text: 'EMAIL', value: 'email' ,width:"100px",},
        { text:'LAST APP',value:"lastdate",width:"150px",},
        { text:'30 Day Total',value:"counter",width:"200px",sortable: false},
        { text:'USER CREATED',value:'created_at',width:"200px",},
        { text: 'ACTION', value: 'actions' },
      ],
      usreList: [],
      status: [
        {text: 'Open',value:0},
        {text: 'Proposed',value:1},
        {text: 'Closed',value:2},
        {text: 'Won',value:3},
        {text: 'Lost',value:4}
      ],
      statusColor,
      // icons
      icons: {
        mdiSquareEditOutline,
        mdiDotsVertical,
      },
    }
  },
  created() {
    this.getUsers();
  },
  methods:{    
    changeRoute(a) {
      this.getUsers()
      },
    DelConfirm(id) {
           this.delId = id;
           this.dialog = true;
    },
    async Status(a,item){
      item.status = a;
      const data = {user:item}
       try{
                 await axios.post(BaseUrl + `status`, data).then(response => (console.log(response)))
                 this.getUsers();
            }
            catch(err){
                console.log(err);
            }  
    },
    Seldate(arrayApp) {
      var date = new Date();
      date.setDate(date.getDate() - this.select.src);
      var finalDate = (date.getMonth()+1) +'/'+ date.getDate()  +'/'+date.getFullYear();      
      var newApp = arrayApp.filter(function(item){return new Date(item.register_date).getTime() > new Date(finalDate).getTime()});
      return newApp.length;
    },
    async getUsers() {
      var _that = this;
      var applicantLst = [];
            try{
                 await axios.get(BaseUrl + `all`).then(response => (_that.usreList = response.data))
                 await axios.get(BaseUrl + `allapplicant`).then(response => (applicantLst = response.data))
                 for(var i=0;i<_that.usreList.length;i++) {
                   var arrApp = applicantLst.filter(function(item){ return _that.usreList[i]['email'] == item.agentemail})
                    if(arrApp.length > 0){
                      _that.usreList[i]['counter'] = await this.Seldate(arrApp);
                              var date = new Date(Math.max(...arrApp.map(e => new Date(e.register_date))));
                      _that.usreList[i]['lastdate'] =  (date.getMonth()+1) +'/'+ date.getDate()  +'/'+date.getFullYear();
                    }

                   if(_that.usreList[i]['created_at']){
                     var d = _that.usreList[i]['created_at'].split('T')
                     _that.usreList[i]['created_at']=d[0];
                   }
                 }

                   this.$cookies.set('userCnt',this.usreList.length);
                 this.componentkey++;
            }
            catch(err){
                console.log(err);
            }   
    },
     async Delete() {
       const data = {
         id : this.delId
       }
            try{
                 await axios.post(BaseUrl + `delete`,data).then(response => (console.log(response)))

                 this.getUsers();
            }
            catch(err){
                console.log(err);
            }   
    },
    EditSubmit(id) {
      this.$router.push(`/user/edit/`+id)
    },
    

  }
}
</script>
